import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { CotizacionModel } from "./cotizacion.model"
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { UnsubscribeOnDestroyAdapter } from "src/app/shared/UnsubscribeOnDestroyAdapter";
import { environment } from "src/environments/environment";
@Injectable({
  providedIn: 'root'
})
export class CotizacionService extends UnsubscribeOnDestroyAdapter {
  isTblLoading = true;
  dataChange: BehaviorSubject<CotizacionModel[]> = new BehaviorSubject<CotizacionModel[]>([]);
  dialogData: any;
  constructor(private httpClient: HttpClient) {
    super();
  }
  get data(): CotizacionModel[] {
    return this.dataChange.value;
  }

  getDialogData() {
    return this.dialogData;
  }

  /** CRUD METHODS */

  getAllCotizaciones(): void {
    this.subs.sink = this.httpClient.get<CotizacionModel[]>(`${environment.apiUrl}/getAllCotizaciones`).subscribe(
      (data) => {
        this.isTblLoading = false;
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        this.isTblLoading = false;
        console.log(error.name + " " + error.message);
      }
    );
  }


  deleteCotizacion(id: number): void {
    console.log(id);

     this.httpClient.delete(`${environment.apiUrl}/deleteCotizacion` + id).subscribe(data => {
      console.log(id);
      },
      (err: HttpErrorResponse) => {
         // error code here
      }
    );
  }
 
}
