import { Component, Inject, OnInit } from '@angular/core';
import { CotizacionService } from '../../cotizacion.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-delete-cotizacion',
  templateUrl: './delete-cotizacion.component.html',
  styleUrls: ['./delete-cotizacion.component.sass']
})
export class DeleteCotizacionComponent {

 constructor(
    public dialogRef: MatDialogRef<DeleteCotizacionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public cotizacionService: CotizacionService
  ) {}
  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.cotizacionService.deleteCotizacion(this.data.id);
    console.log('Hola');
  }

}
